//reducer是用来处理数据的
//获取datalist的
const homereducers = (state={datas:[]},action)=>{
    switch(action.type){
        case 'DATALIST':
            // Object.assign({},state,{username:'123'})
            return {datas:action.datas}
        default:
            return state;
        
    }
}

export default homereducers;