//reducer是用来处理数据的
//获取shoulist的

function todoList(state=[],action){
    if(action.type =='SHOULIST'){
        //不可变对象
        console.log('reducer',action);
        return [...state,action.value]
    }
    if(action.type =='DEL_TODO'){
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }


    return state;
}

export default todoList;