import Home from './wuyu/Home';
import Login from './wuyu/Login';
import Cart from './wuyu/Cart';
import List from './wuyu/List';
import Shou from './wuyu/Shou';
import My from './wuyu/My';
import Listone from './wuyu/Listone';
import store from './wuyu/store'
import Search from './wuyu/Search';
import {Provider} from 'react-redux'


