import React from 'react'
import './listone.css'
import { NavBar, Icon, Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux'

class Listone extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
            hidden: false,
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div className="con">
                <div className="part">
                    <div className="im">
                        <img src={`https://img.alicdn.com/imgextra/i1/158441784/O1CN01fbVLsF1P373i1dj8u_!!158441784.jpg_640x640q80_.webp`} alt="" />
                    </div>
                    <div className="parttwo">￥40</div>
                    <div className="partone">HZ大容量ins耐高温玻璃饮料杯早餐牛奶杯果汁带盖吸管星星水杯</div>
                    <div className="partthree" onClick={() => this.props.history.go(-1)}>
                        <Icon type="left" size="lg" color="white" />
                    </div>
                </div>
                <div className="wei">
                    <ul>
                        <li className="wei-t1">
                            <i className='iconfont icon-dianpu'></i>
                            <p>店铺</p>
                        </li>
                        <li className="wei-t1"><i className='iconfont icon-kefu'></i><p>客服</p></li>
                        <li className="wei-t1">
                            {/* <i className='iconfont icon-shoucang'></i> */}
                            <i className="shou">
                                <TabBar
                                    // unselectedTintColor="#949494"
                                    tintColor="red"
                                    barTintColor="white"
                                    hidden={this.state.hidden}
                                >
                                    <TabBar.Item
                                        // title="收藏"
                                        // key="home"
                                        icon={
                                            <i className='iconfont icon-shoucang'></i>
                                        }
                                        selectedIcon={
                                            <i className='iconfont icon-shoucang2'></i>
                                        }
                                        selected={this.state.selectedTab === 'greenTab'}
                                        onPress={() => {
                                            this.setState({
                                                selectedTab: 'greenTab',
                                            });
                                        }}
                                        data-seed="logId"
                                    >
                                    </TabBar.Item>

                                </TabBar>

                            </i>
                            <p>收藏</p>

                        </li>
                        <li className="wei-b1">加入购物车</li>
                        <li className="wei-b2">立即购买</li>
                    </ul>
                </div>

    

            </div>


        )
    }


}

export default withRouter(Listone);


