
import React from 'react'
// import { Carousel, WingBlank } from 'antd-mobile';
// import { withRouter, Link, Router, Switch, Route } from 'react-router-dom';
// import { SearchBar, Button, WhiteSpace } from 'antd-mobile';
import './home.css'
import { NavBar, Icon } from 'antd-mobile';
import Listone from './Listone';
import Mytab from './Mytab';
import { connect } from 'react-redux'
import { datalist } from './actionCreators'
import {login} from './actionCreator'

const Home = (props) => {

    const splist = () => {
        props.dispatch(datalist())

    }


      // props.history.push(`/listone/:${item.id}`)
    const routerTo = (item) =>{
        props.history.push({pathname: `/listone/:${item.id}`, state: {data:item}})
    }
    return (


        <div className="first" style={{
            // position: 'fixed',
            width: '100%',
            // bottom: 0
        }}>
            <Mytab />

            <div className="sea" >
                <i className='iconfont icon-taobao1'></i>
                <button onClick={() => props.history.push('/search')}>
                    <Icon key="0" type="search" style={{ marginLeft: '80px' }} />
                    <span>寻找宝贝店铺</span>
                </button>
            </div>


            <div className="bai">
                <div>
                    <ul>
                        {/* <li><i className='iconfont icon-zu'></i></li> */}
                        <li><i className='iconfont icon-xin1'></i></li>
                        <li>你可能还喜欢</li>
                        {/* <li><i className='iconfont icon-zu1'></i></li> */}
                    </ul>
                </div>

            </div>

            <div className="list" onLoad ={splist()}>
                {/* 请求数据，派发action，存到store中 */}
                <ul>
                    {
                        props.datas.map((item) => {
                            return <li key={item.id} className="" onClick={() => routerTo(item)}>
                                <img src={item.img} alt="" />
                                <span className="one">{item.title}</span>
                                <span className="two">￥{item.price}</span>
                            </li>
                        })
                    }
                </ul>
            </div>

            <div className="end">


            </div>


        </div>
    );

  
}


const mapStateToProps = (state) => ({
    datas: state.home.datas
})

export default connect(mapStateToProps)(Home);


