// import { ActivityIndicator } from 'antd-mobile'
import React ,{useState} from 'react'
import { connect } from 'react-redux'
import {login} from './actionCreator'
import Mytab from './Mytab';

const Login = (props) => {
    // const [isShow,setShow] = useState(false);
    const log = () => {
        props.dispatch(login())
    }


    return (
       
        <div>
            <Mytab/>
            {props.username ? <div>{props.username}<button onClick={() => props.history.go(-1)}>可以观看收藏商品列表或者添加收藏商品了，请点击</button></div> : <button onClick={log}>登录</button>}

            {/* <ActivityIndicator toast text='登录中...' animating={isShow} /> */}
        </div>
    )
}

const mapStateToProps = (state) => ({
    username: state.login.username
})

export default connect(mapStateToProps)(Login);