import React, { useState } from 'react'
import './listone.css'
import { NavBar, Icon, Button, TabBar } from 'antd-mobile';

import { connect } from 'react-redux'


const Listone = (props) => {


    let [state, setState] = useState({
        selectedTab: 'blueTab',
        hidden: false,
    });
    let count = 0;

    const spList = props.location.state.data

    return (


        <div className="con">

            <div className="part">
                <div className="im">
                    <img src={spList.img} alt="" />
                </div>
                <div className="parttwo">￥{spList.price}</div>
                <div className="partone">{spList.title}</div>
                <div className="partthree" onClick={() => props.history.go(-1)}>
                    <Icon type="left" size="lg" color="black" />
                </div>
            </div>


            <div className="wei">
                <ul>
                    <li className="wei-t1">
                        <i className='iconfont icon-dianpu'></i>
                        <p>店铺</p>
                    </li>
                    <li className="wei-t1"><i className='iconfont icon-kefu'></i><p>客服</p></li>
                    <li className="wei-t1" >

                        <i className="shou">
                            <TabBar
                                // unselectedTintColor="#949494"
                                tintColor="red"
                                barTintColor="white"
                                hidden={state.hidden}
                            >
                                <TabBar.Item
                                    // title="收藏"
                                    // key="home"

                                    icon={
                                        <i className='iconfont icon-shoucang'></i>
                                    }
                                    selectedIcon={
                                        <i className='iconfont icon-shoucang2'></i>
                                    }
                                    
                                    selected={state.selectedTab === 'greenTab'}
                                    onPress={(e) => {
                                        if (props.username) {
                                            setState({
                                                selectedTab: 'greenTab',
                                            });

                                            props.dispatch({ type: 'SHOULIST', value: spList })


                                            console.log(spList)
                                        } else {
                                            props.history.push({ pathname: `/login` })                                           
                                        }

                                    }}
                                    data-seed="logId"
                                >
                                </TabBar.Item>

                            </TabBar>

                        </i>


                    </li>
                    <li className="wei-b1">加入购物车</li>
                    <li className="wei-b2">立即购买</li>
                </ul>
            </div>




        </div>


    )


}

const mapStateToProps = (state) => ({
    datas: state.home.datas,
    username: state.login.username
})

export default connect(mapStateToProps)(Listone);
// export default Listone;


