import React ,{useEffect}from 'react'
import { connect } from 'react-redux'
import Mytab from './Mytab';

const Shou = (props) => {
     
    useEffect(()=>{
        if(!props.username){
            props.history.push({
                pathname:'/login',
            })
        }
    },[])

    
    return (
        <div>
            <Mytab/>
            {
                props.mydata.map((item,idx) => (
                    <li key={idx}>
                        <img src={item.img} alt="" />
                        <span className="one">{item.title}</span>
                        <span className="two">￥{item.price}</span>

                        <button onClick={() => {
                            props.dispatch({
                                type: "DEL_TODO",
                                index: idx
                            })
                        }}>删除</button>

                    </li>
                ))
            }
        </div>
    )
}

const mapStateToProps = (state) => {
    
     console.log(state)
    return {
        mydata: state.shou,
        username: state.login.username
    }

}


export default connect(mapStateToProps)(Shou)